/** @format */

import { Dialog, DialogContent, DialogTrigger } from "@/components/ui/dialog";
import { TaskForm } from "./task-form";
import { TaskListProps, TaskFormProps } from "@/types"; // Import TaskFormProps
import { useState } from "react";

export const TaskModal = ({
  button,
  data, // This is initialData for the form
  onSuccess, // This is the onSuccess callback from the parent (TasksPage)
  onCancel,  // This is the onCancel callback from the parent (TasksPage)
}: {
  button: React.ReactNode;
  data?: TaskListProps;
  onSuccess?: TaskFormProps['onSuccess']; // Use TaskFormProps for type
  onCancel?: TaskFormProps['onCancel'];   // Use TaskFormProps for type
}) => {
  const [isOpen, setIsOpen] = useState<boolean>(false); // Manage Dialog's open state internally

  // Define a local onSuccess handler that closes the modal and then calls parent's onSuccess
  const handleSuccessAndClose = () => {
    setIsOpen(false); // Close the modal
    if (onSuccess) {
      onSuccess(); // Call the parent's onSuccess (which handles closing form & clearing data)
    }
  };

  // Define a local onCancel handler that closes the modal and then calls parent's onCancel
  const handleCancelAndClose = () => {
    setIsOpen(false); // Close the modal
    if (onCancel) {
      onCancel(); // Call the parent's onCancel
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>{button}</DialogTrigger>
      <DialogContent>
        <TaskForm
          initialData={data}
          onSuccess={handleSuccessAndClose} // Pass our local handler
          onCancel={handleCancelAndClose}   // Pass our local handler
        />
      </DialogContent>
    </Dialog>
  );
};